import React from "react";
import { AnalyticService } from "../../Services/AnalyticService";
import contact from "../../images/contact.svg";
import "./ContactForm.css";

const mailContacto = "contacto@cristoreyangol.cl";

function ContactForm() {
  return (
    <div className="formContainerHome" id="contacto">
      <h3>Escríbenos un mensaje</h3>
      <div className="contactFormMain">
        <img
          className="contactFormAdress"
          src={contact}
          title="dirección para contacto"
          alt="dirección para contacto"
          width={275}
          height={25}
          loading="lazy"
        />
        <a
          href={`mailto:${mailContacto}?subject=Quiero dejar un mensaje`}
          className="btnSubmitForm"
          onClick={() =>
            AnalyticService.event("Interacciones-enviar_Correo-MailtoHome")
          }
        >
          <p className="formContactBtn">Escríbenos</p>
          <svg
            xmlns="https://www.w3.org/2000/svg"
            width="16"
            height="16"
            fill="currentColor"
            className="bi bi-envelope"
            viewBox="0 0 16 16"
          >
            <path d="M0 4a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v8a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V4Zm2-1a1 1 0 0 0-1 1v.217l7 4.2 7-4.2V4a1 1 0 0 0-1-1H2Zm13 2.383-4.708 2.825L15 11.105V5.383Zm-.034 6.876-5.64-3.471L8 9.583l-1.326-.795-5.64 3.47A1 1 0 0 0 2 13h12a1 1 0 0 0 .966-.741ZM1 11.105l4.708-2.897L1 5.383v5.722Z" />
          </svg>
        </a>
      </div>
    </div>
  );
}

export default ContactForm;
